<template>
	<div>
		<header class="head">
		   <img src="${pageContext.request.contextPath}/image/news/leftBack.png" onclick="window.history.go(-1)" alt=""/>
		    <span></span>
		</header>
		<div class="newsMain">
		    <!---->
			<div class="nav tab">
				<a href="javascript:;" class="item"  v-for="(item,index) in dateValue" :class="{cur:index==num}">
                     <dl>
                         <dt>
                         	<span>{{item.integralValue}}</span>
                         </dt>
                         <dd>{{item.nextDay}}</dd>
                     </dl>
                </a>		       
		    </div>
	    </div>
	<div class="register">
	    <button @click="register()"></button>
	    <ul>
	        <li><span id="signDate1">{{registerList.consecutiveNum}}</span></li>
	        <li><span  id="point">{{registerList.totalPoint}}</span></li>
	    </ul>
	</div>
	<div class="act-rule">
	    <div>
	        <h3></h3>
	        <p><span id="totalDate">0</span><span id="totalJf">0</span></p>
	    </div>
	</div>

	<!--<div class="model2" id="model2" style="display:none">
		   <div class="modelBox">
		       <div class="modelTxt">		          
		           <div id="formFirst">
		               <ul>
		                  <li><label></label><input type="text" id="phoneNum" value="" v-on:focus="" v-on:blur="" style="width:56%;border:0;background:transparent;color:#ffffff;
    outline: none;"/>&nbsp;</li>
		                  <li><label></label><input type="text" id="phonecode" value="" style="width:27%;border:0;background: transparent;color:#ffffff;
    outline: none;"/><input type="button" class="btn" id="btnsms" value=""/></li>
    						<li><label></label><input type="text" id="passWord" value="" style="width:56%;border:0;background: transparent;color:#ffffff;
    outline: none;"/>&nbsp;</li>
		                  <li class="btn"><button @click="bindbyphone()" class="sure"></button></li>
		                </ul>
		            </div>
		        </div>
		    </div>
	</div>-->

	<div class="model2" id="model2" style="display:none">
		   <div class="modelBox" style="left:9%;width:80%">
		       <div class="modelTxt">		          
		           <div id="formFirst">
		               <ul>
		                  <li><label><img src="../assets/image/name.png" style="vertical-align:middle;width:48%"/></label><input type="text" id="telPhoneNum" v-on:focus="" v-on:blur="" value="" style="width:56%;border:0;background:transparent;color:#ffffff;
    outline: none;"/>&nbsp;</li>
		                  <li><label><img src="../assets/image/suo.png" style="vertical-align:middle;width:40%"/></label><input v-on:focus="" v-on:blur="" type="text" id="pass" value="" style="width:27%;border:0;background:transparent;color:#ffffff;outline:none;"/></li>
    						<li style="border:0;"><label style="width:5.7rem;"><input type="checkbox" name="" style="vertical-align:middle;"></label><router-link :to="{path:'login'}" tag="a" ></router-link>&nbsp;</li>
    						<li class="btn"><button @click="signUp()" class="sure"></button></li>
    						<li style="text-align:center;border :0;"> <router-link style="color:red;redtext-decoration:underline;" :to="{path:'login'}" tag="a" ></router-link></li>
    						<li style="text-align:center;border:0;line-height:0.6rem;"></li>		                 
		                </ul>
		            </div>
		        </div>
		    </div>
		</div>
	</div>
</template>
<script type="text/javascript">
    import {setCookie,getCookie} from '../assets/js/cookie.js'
var id;
	
export default {
    data() {
      return{      
        registerList:[],
        dateValue:[],
        arr:[],
        isActive:true,
        num:0      
      }
    },
    components: { // 

    },
    mounted:function () {
    	id = this.$route.query.memberId
      //setCookie('id',id,1000*60);

    	//alert(id)
    	 //
      axios.get('http://www.shllzx.com/llzx//appController/appSignInitForm', {params: {memberId: id}}).then(res=>{
        console.log(res);
        this.registerList=res.data;
        this.dateValue=res.data.data;
	      console.log(this.dateValue);
	     console.log(res.data);

      }).catch(error=>console.log(error));
    },
    methods: {	 
	   register(){
	   	 $.ajax({
             	url:'http://www.shllzx.com/llzx//appController/appSign',
                 type: "post",
                 data: {
                 	"memberId":id
                 	
                 },
                  dataType: 'json',
                
             }).then(function (res) {
                alert(res.msg);
//json
                self.arr = res;
               // alert(self.model)

                if(res.code == "1"){
                        alert(res.msg);
                        
                  }

            }).fail(function () {
                console.log('');
            })
	      }
	   
       
      },

      watch: {
          $route(e){
            alert(123456);
            console.log(e);

      
          }
      }
     
    
  }





</script>
<style type="text/css">
  body{
    margin:0;
    padding:0;
  }
</style>

<style type="text/css" scoped>
  @import '../assets/css/register.css'
</style>


